/// Line clamp mixin.
/// @param {string|int} $lines
/// @return {*} queried line clamp styles.
@mixin line-clamp($lines) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  @if $lines == 0 or $lines == 'none' {
    -webkit-line-clamp: unset;
  } @else {
    -webkit-line-clamp: $lines;
  }
}